import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { MarketSearchProvider } from './context/MarketSearch';
import { IncomingListProvider } from './context/IncomingList';
import { OutgoingListProvider } from './context/OutgoingList ';
import { MiscProvider } from './context/Misc';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <MiscProvider>
    <MarketSearchProvider>
      <IncomingListProvider>
        <OutgoingListProvider>
          <App />
        </OutgoingListProvider>
      </IncomingListProvider>
    </MarketSearchProvider>
  </MiscProvider>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
